                            <v-card-text>
                                浙江大学是一所特色鲜明、在海内外有较大影响的综合型、研究型、创新型大学，其学科涵盖哲学、经济学、法学、教育学、文学、历史学、艺术学、理学、工学、农学、医学、管理学等十二个门类。设有7个学部、37个专业学院（系）、1个工程师学院、2个中外合作办学学院、7家附属医院。学校现有紫金港、玉泉、西溪、华家池、之江、舟山、海宁等7个校区，占地面积5870761平方米，校舍总建筑面积2746377平方米，图书馆总藏书量777.8万册。截至2018年底，浙江大学现有全日制在校学生54641人（其中：本科生25425人，硕士研究生19038人，博士研究生10178人），国际学生7074人（其中：攻读学位的国际学生4409人）。有教职工8909人（其中：专任教师3741人），教师中有中国科学院院士、中国工程院院士（含双聘）45人、文科资深教授10人、教育部“长江学者奖励计划”特聘（讲座）教授121人、国家杰出青年科学基金获得者133人。在国家公布的“双一流”建设名单中，学校入选一流大学建设高校（A类），18个学科入选一流建设学科，居全国高校第三。根据2019年5月ESI公布的数据，学校有8个学科进入世界学术机构排名前1‰，居全国高校第一；有1个学科进入世界学术机构排名前万分之一，有8个学科进入世界学术机构排名前100位，均居全国高校第二；有5
                                个学科进入世界学术机构排名前50位，居全国高校第三。 
