                                <p >浙江大学紧紧围绕“德才兼备、全面发展”的核心要求，全面落实立德树人根本任务，着力培养德智体美劳全面发展、具有全球竞争力的高素质创新人才和领导者。在长期的办学历程中，涌现出大批著名科学家、文化大师以及各行各业的精英翘楚，校友中当选为两院院士的有200余人。学校始终保持与时俱进的教育思想，教育教学模式改革走在了全国高校前列；丰富的校园文化、先进的教学设施和广泛的国际交流为学生成长创造了优越条件。2018届毕业生初次就业率达到97.64%，本科毕业生海内外深造率达到60.30%。</p><p class="p_text_indent_2">浙江大学注重精研学术和科技创新，建设了一批开放性、国际化的高端学术平台，汇聚了各学科的学者大师和高水平研究团队。近年来，学校发明专利授权数、权威学术期刊论文发表数、科研总经费等主要科研指标保持全国高校领先地位，在科学技术和人文社科领域取得了丰硕成果。学校主动对接国家和区域重大战略需求，着力打造高水平的创新源、人才泵和思想库。“十二五”以来，学校作为牵头单位获得国家科技进步特等奖1项、一等奖7项、二等奖40项；《中国历代绘画大系》、《中华礼藏》、敦煌学等文化传承创新成果在海内外产生了广泛影响。</p>
                                
