<template>
  <div>
    <v-layout row wrap>
      <v-flex xs10 offset-xs1>
        <v-banner>
          输入用户名来查询
          <template v-slot:actions>
            <v-flex xs6 offset-xs2>
              <v-text-field
                v-model=" queryName"
                :counter="10"
                :rules="[v => !!v || 'Not empty!']"
                label="用户名"
                required
              ></v-text-field>
            </v-flex>

            <v-btn text color="primary" @click="query">查询</v-btn>
            <v-btn text color="primary" @click="back">查看全部</v-btn>
          </template>

        </v-banner>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="ml-3 mr-3" v-if="all">
      <v-flex xs6 class="pl-5 pr-5 mt-5 mb-5" v-for="(item, index) in getUsers" :key="index">
        <v-card class="mx-auto" color="#26c6da" dark hover>
          <v-card-title>
            <v-icon large left>mdi-twitter</v-icon>
            <span class="title font-weight-light">FKD 宇治没有波·婷</span>
          </v-card-title>

          <v-card-text class="headline font-weight-bold">
            <v-container>入站时间：{{item.createdAt}}</v-container>
            <v-container>联系方式：{{item.contact}}</v-container>
          </v-card-text>

          <v-card-actions>
            <v-list-item class="grow">
              <v-list-item-avatar color="grey darken-3">
                <v-img
                  class="elevation-6"
                  src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
                ></v-img>
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title>{{item.username}}</v-list-item-title>
              </v-list-item-content>

              <v-row align="center" justify="end">
                <v-btn color="success" @click="nope">想知道密码么？</v-btn>
              </v-row>
            </v-list-item>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="ml-3 mr-3" v-if="queried">
      <v-flex xs6 class="pl-5 pr-5 mt-5 mb-5" v-for="(item, index) in getQueriedUser" :key="index">
        <v-card class="mx-auto" color="#26c6da" dark hover>
          <v-card-title>
            <v-icon large left>mdi-twitter</v-icon>
            <span class="title font-weight-light">FKD 宇治没有波·婷</span>
          </v-card-title>

          <v-card-text class="headline font-weight-bold">
            <v-container>入站时间：{{item.createdAt}}</v-container>
            <v-container>联系方式：{{item.contact}}</v-container>
          </v-card-text>

          <v-card-actions>
            <v-list-item class="grow">
              <v-list-item-avatar color="grey darken-3">
                <v-img
                  class="elevation-6"
                  src="https://avataaars.io/?avatarStyle=Transparent&topType=ShortHairShortCurly&accessoriesType=Prescription02&hairColor=Black&facialHairType=Blank&clotheType=Hoodie&clotheColor=White&eyeType=Default&eyebrowType=DefaultNatural&mouthType=Default&skinColor=Light"
                ></v-img>
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title>{{item.username}}</v-list-item-title>
              </v-list-item-content>

              <v-row align="center" justify="end">
                <v-btn color="success" @click="nope">想知道密码么？</v-btn>
              </v-row>
            </v-list-item>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import Bmob from "hydrogen-js-sdk";
import { store } from "../store/index";

export default {
  data() {
    return {
      queryName: "",
      all: true,
      queried: false
    };
  },
  computed: {
    getUsers() {
      return store.getters.getUsers;
    },
    getQueriedUser() {
      return store.getters.getQueriedUser(this.queryName);
    }
  },
  methods: {
    nope() {
      alert("想都别想！");
    },

    query() {
      store.commit("setUsers", Bmob.Query("guest"));
      this.all = false;
      this.queried = true;
    },

    back() {
      store.commit("setUsers", Bmob.Query("guest"));
      this.all = true;
      this.queried = false;
    }
  },
  mounted() {
    store.commit("setUsers", Bmob.Query("guest"));
  }
};
</script>