import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../views/Dashboard.vue'
import xing from '../views/xing.vue'

import li from '../views/li.vue'

import together from '../views/together'
import myMeetup from '../views/mymeetup'
import address from '../views/address'


import xingmap from '../views/xingmap.vue'
import maps from '../views/maps.vue'
import second from '../views/second.vue'
import third from '../views/third.vue'
import four from '../views/four.vue'



Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'dashboard',
    component: Dashboard
  },
  {
    path:'/together',
    name:'together',
    component:together,
    
  },
  {
    path:'/mine',
    name:'mine',
    component:myMeetup
  },
  {
    path:'/address',
    name:'address',
    component:address
  },
  {//�����router���������ӣ��ļ�����component��
    path: '/xing',
    name: 'xing',
    component: xing
  },
  {//�ϵ���router���������ӣ��ļ�����component��
    path: '/li',
    name: 'li',
    component: li
  },
  {
    path:'/second',
    name:second,
    component:second
  },
  {
    path:'/third',
    name:third,
    component:third
  },
  {
    path:'/four',
    name:four,
    component:four
  },
  

  {
    path: '/xingmap',
    name: 'xingmap',
    component: xingmap
  },
  {
    path: '/xingmap/:id',
    name: 'maps',
    props:true,
    component: maps
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
