<template>
  <!---->
  <div>
    <v-layout row wrap>
      <v-flex xs10 offset-xs1>
        <v-banner>
          当前已有{{getTotalNum}}人次进行攒局
          <template v-slot:actions>
            <v-btn text color="primary" @click="goMine">我加入的局</v-btn>
            <v-btn text color="primary" @click="check">发起攒局</v-btn>
          </template>
        </v-banner>
        <v-dialog v-model="dialog" max-width="490">
          <v-card>
            <v-card-title class="headline">
              <v-select
                outlined
                v-model="selectedType"
                label="选择攒局类型"
                :items=" meetupType"
                prepend-icon="fa-info"
                autofocus
              ></v-select>
            </v-card-title>
            <v-card-text>
              <v-form ref="form" v-model="valid">
                <v-text-field
                  v-model="top"
                  :rules="[v => v>1&& Math.floor(v)===v || '应输入一个大于1的整数!']"
                  label="请输入指定人数"
                  required
                ></v-text-field>
                <v-text-field
                  v-model="detail"
                  :rules="[v => !!v || 'Not empty!']"
                  label="拼单详情"
                  required
                ></v-text-field>
                <v-layout row wrap>
                  <v-flex xs2 offset-xs10>
                    <v-btn :disabled="!valid" color="success" class="mr-4" @click="makeMeetup">确认</v-btn>
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="mt-8 ml-4 mr-4">
      <v-flex xs6 class="pl-3 pr-3 mt-3 mb-3" v-for="(item, index) in getMeetups" :key="index">
        <v-card>
          <div primary-title style="height:40px" class="pt-2">
            <v-layout row wrap>
              <v-flex xs1 offset-xs1>
                <v-icon>{{meetupIcons[item.typeIndex]}}</v-icon>
              </v-flex>
              <v-flex xs4>
                <span class="text-no-wrap font-weight-bold">{{meetupType[item.typeIndex]}}</span>
              </v-flex>
              <v-flex xs3 offset-xs2>当前进度：{{item.participants.length}}/{{item.top}}</v-flex>
            </v-layout>
          </div>
          <v-divider></v-divider>
          <v-card-text>
            <v-layout row wrap>
              <v-flex xs10 offset-xs1 class="mb-3">
                <v-sheet height="100">
                  详情：{{item.detail}}
                </v-sheet>
              </v-flex>
              <v-flex xs8 offset-xs1 class="mb-2">
                <div class="mb-2">发起人：{{item.starter}}</div>
                <div>联系方式：{{item.contact}}</div>
              </v-flex>
              <v-flex xs3>
                <v-btn
                  :color="meetupColors[item.typeIndex]"
                  class="pt-1"
                  @click="participate(index,item.objectId)"
                >加入</v-btn>
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-progress-linear
            :color="meetupColors[item.typeIndex]"
            :value="(item.participants.length/item.top)*100"
          ></v-progress-linear>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>


<script>
import Bmob from "hydrogen-js-sdk";
import { store } from "../store/index";
export default {
  computed: {
    getMeetups() {
      return store.getters.getOtherMeetups;
    },
    getTotalNum() {
      return store.getters.getTotalNum;
    },
    curUser() {
      return store.getters.curUser;
    }
  },

  mounted() {
    store.commit("setTotalNum", Bmob.Query("mtn"));
    store.commit("setMeetups", Bmob.Query("meetup"));
  },

  data() {
    return {
      totalNum: 0,
      statId: "Mf2WDDDH",
      meetupType: ["拼单", "吃喝玩乐", "自习", "拼车", "组个夸夸群", "其他"],
      meetupIcons: [
        "fa-rmb",
        "fa-coffee",
        "fa-book",
        "fa-car",
        "fa-thumbs-up",
        "fa-users"
      ],
      meetupColors: [
        "pink lighten-2",
        "deep-purple lighten-2",
        "blue lighten-3",
        "cyan darken-2",
        "red lighten-3",
        "amber lighten-2"
      ],
      selectedType: [],
      dialog: false,
      valid: false,
      top: 2,
      detail: "",
      c: "primary"
    };
  },
  methods: {
    check() {
      if (this.curUser == "") alert("请先登录");
      else this.dialog = true;
    },
    getColor() {
      return "primary";
    },
    goMine() {
      if (this.curUser == "") {
        alert("请先登录");
      } else this.$router.push({ path: "/mine" });
    },
    // async addOne() {
    //   await Bmob.Query("mtn")
    //     .get(this.statId)
    //     .then(res => {
    //       res.increment("mtn");
    //       res.save();
    //     });
    // },
    makeMeetup() {
      var user = store.getters.curUser;
      var guest = Bmob.Query("guest");
      var meetup = Bmob.Query("meetup");
      var mtn = Bmob.Query("mtn");

      if (user == "") {
        alert("请先登录");
        this.dialog = false;
        return;
      }
      guest.equalTo("username", "==", user);
      guest.find().then(res => {
        console.log(res);
        var userInfo = res[0];
        meetup.set("starter", user);
        meetup.set("contact", userInfo.contact);
        meetup.set("detail", this.detail);
        meetup.addUnique("participants", [user]);
        meetup.set("top", eval(this.top));
        meetup.set("typeIndex", this.meetupType.indexOf(this.selectedType));
        meetup.save().then(res => {
          console.log(res);
          store.commit("setMeetups", meetup);
          store.commit("addMeetups", mtn);
          this.dialog = false;
          alert("发起攒局成功！可以在“我加入的局”中查看");
        });
      });
    },

    participate(index, id) {
      var meetup = Bmob.Query("meetup");
      if (this.curUser == "") alert("请先登录");
      else {
        console.log(index, id);
        meetup.get(id).then(res => {
          res.addUnique("participants", [this.curUser]);
          res.save().then(res2 => {
            console.log(res2)
            alert('加入成功，可在我的攒局里查看')
            store.commit("setMeetups", meetup);
            store.commit("addMeetups", Bmob.Query("mtn"));
          });
        });
      }
    }
  }
};
</script>