    mutations: {
        setLogin(state, val) {
            state.hasLogin = val
        },
        setCurUser(state, val) {
            state.curUser = val
        },
        setMeetups(state, query) {
            query.find().then(res => {
                state.meetups = res
                // console.log(res)
            })
        },
        addMeetups(state, query) {
            query.get("Mf2WDDDH")
                .then(res => {
                    res.increment("mtn")
                    res.save()
                    state.totalNum++
                })
        },
        delMeetups(state,query){
            query.get("Mf2WDDDH")
            .then(res => {
                var prev = res.mtn
                res.set('mtn',prev-1)
                res.save()
                state.totalNum--
            })
        },
        setTotalNum(state, query) {
            query.get("Mf2WDDDH")
                .then(res => {
                    state.totalNum = res.mtn
                })
        },

        setUsers(state,query){
            query.find().then(res=>{
                state.users=res
            })
        }

    },
    actions: {},
    getters: {
        loadedCards(state) {
            return state.loadedCards
        },
        loadedCard(state) {
            return (cardID) => {
                return state.loadedCards.find((card) => {
                    return card.id === cardID
                })
            }
        },
        hasLogin(state) {
            return state.hasLogin
        },
        curUser(state) {
            return state.curUser
        },
        getMeetups(state) {
            return state.meetups
        },
        getMyMeetups(state){
            return state.meetups.filter(v=>{
                return v.participants.indexOf(state.curUser)!=-1
            })
        },
        getOtherMeetups(state){
            return state.meetups.filter(v=>{
                return v.participants.indexOf(state.curUser)==-1&&v.participants.length<v.top
            })
        },
        getTotalNum(state) {
            return state.totalNum
        },

        getUsers(state){
            return state.users
        },
        getQueriedUser(state){
            return (name)=>{
                return state.users.filter((val)=>{
                    return val.username==name
                })
            }
        }
    }

