<template>
  <div>
    <v-layout row wrap>
      <v-flex xs10 offset-xs1>
        <v-banner>
          我参加了{{getMyMeetups.length}}个攒局
          
          <template v-slot:actions>
            <v-btn text color="primary" @click="goBack">返回</v-btn>
          </template>
        </v-banner>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="mt-8 ml-4 mr-4">
      <v-flex xs6 class="pl-3 pr-3 mt-3 mb-3" v-for="(item, index) in getMyMeetups" :key="index">
        <v-card>
          <div primary-title style="height:40px" class="pt-2">
            <v-layout row wrap>
              <v-flex xs1 offset-xs1>
                <v-icon>{{meetupIcons[item.typeIndex]}}</v-icon>
              </v-flex>
              <v-flex xs4>
                <span class="text-no-wrap font-weight-bold">{{meetupType[item.typeIndex]}}</span>
              </v-flex>
              <v-flex xs3 offset-xs2>当前进度：{{item.participants.length}}/{{item.top}}</v-flex>
            </v-layout>
          </div>
          <v-divider></v-divider>
          <v-card-text>
            <v-layout row wrap>
              <v-flex xs10 offset-xs1 class="mb-3">
                <v-sheet height="130">
                  <div class="mt-2 mb-2">
                    详情：{{item.detail}}
                  </div>
                  <div class="mt-2 mb-2 ">
                    <v-chip v-for="(p, i) in item.participants" :key="i">
                      用户{{i+1}}:{{p}}
                    </v-chip>
                  </div>
                  <v-spacer></v-spacer>
                  <div class="font-weight-light mt-5">Tips:你可以在通讯录中找到他们</div>


                </v-sheet>
              </v-flex>
              <v-flex xs8 offset-xs1 class="mb-2">
                <div class="mb-2">发起人：{{item.starter}}</div>
                <div>联系方式：{{item.contact}}</div>
              </v-flex>
              <v-flex xs3>
                <v-btn
                  :color="meetupColors[item.typeIndex]"
                  class="mt-2"
                  @click="quit(index,item.objectId)"
                >退出</v-btn>
              </v-flex>
            </v-layout>
          </v-card-text>
          <v-progress-linear
            :color="meetupColors[item.typeIndex]"
            :value="(item.participants.length/item.top)*100"
          ></v-progress-linear>
        </v-card>
      </v-flex>
    </v-layout>
  </div>
</template>


<script>
import Bmob from "hydrogen-js-sdk";
import { store } from "../store/index";
export default {
  computed: {
    getMeetups() {
      return store.getters.getOtherMeetups;
    },
    getMyMeetups() {
      return store.getters.getMyMeetups;
    },
    getTotalNum() {
      return store.getters.getTotalNum;
    },
    curUser() {
      return store.getters.curUser;
    }
  },

  mounted() {
    store.commit("setTotalNum", Bmob.Query("mtn"));
    store.commit("setMeetups", Bmob.Query("meetup"));
  },

  data() {
    return {
      totalNum: 0,
      statId: "Mf2WDDDH",
      meetupType: ["拼单", "吃喝玩乐", "自习", "拼车", "组个夸夸群", "其他"],
      meetupIcons: [
        "fa-rmb",
        "fa-glass",
        "fa-book",
        "fa-car",
        "fa-thumbs-up",
        "fa-users"
      ],
      meetupColors: [
        "pink lighten-2",
        "deep-purple lighten-2",
        "blue lighten-3",
        "cyan darken-2",
        "red lighten-3",
        "amber lighten-2"
      ],
      selectedType: [],
      dialog: false,
      valid: false,
      top: 2,
      detail: "",
      c: "primary"
    };
  },
  methods: {
    check() {
      if (this.curUser == "") alert("请先登录");
      else this.dialog = true;
    },

    goBack() {
      this.$router.push({ path: "/together" });
    },

    quit(index, id) {
      var meetup = Bmob.Query("meetup");
      if (this.curUser == "") alert("请先登录");
      else {
        console.log(index, id);
        meetup.get(id).then(res => {
          res.remove("participants", [this.curUser]);
          res.save().then(res2 => {
            console.log(res2,'res2');
            console.log(res.participants)
            if (this.curUser == this.getMyMeetups[index].starter) {
              alert("这局没你可就黄了呀！！！");
              meetup.destroy(id)
            } else alert("放鸽子可不是好事情哦！！！");
            store.commit("setMeetups", meetup);
            store.commit("delMeetups", Bmob.Query("mtn"));
          });
        });
      }
    }
  }
};
</script>