
<template>
  <nav>
    
    <v-app-bar flat app class="primary">
      <v-app-bar-nav-icon class="white--text" elevation-10 @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title class="text-uppercase white--text">
        <span class="font-weight-light mr-3">服科导</span>
        <span>宇智没有波·婷</span>
        <v-dialog v-model="dialog" max-width="490">
          <v-card>
            <v-card-title class="headline">
              <v-select
                outlined
                v-model="selectedOp"
                label="注册/登录"
                :items="opList"
                prepend-icon="fa-info"
                autofocus
              ></v-select>
            </v-card-title>
            <v-card-text>
              <v-form ref="form" v-model="valid">
                <v-text-field
                  v-model="username"
                  :counter="10"
                  :rules="[v => !!v || 'Not empty!']"
                  label="用户名"
                  required
                ></v-text-field>
                <v-text-field
                  v-model="password"
                  :counter="15"
                  :rules="[v => !!v || 'Not empty!']"
                  label="密码"
                  required
                ></v-text-field>
                <v-text-field
                  v-if="this.selectedOp=='注册'"
                  v-model="contact"
                  :counter="20"
                  :rules="[v => !!v || 'Not empty!']"
                  label="联系方式"
                ></v-text-field>

                <v-btn :disabled="!valid" color="success" class="mr-4" @click="validate">确认</v-btn>
              </v-form>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <span v-if="hasLogin" class="white--text mr-5">你好，{{curUser}}</span>
      <v-btn text class="cyan darken-1" v-if="!hasLogin" @click="dialog=true">
        <span class="white--text">Sign In</span>
        <v-icon right class="white--text">mdi-login</v-icon>
      </v-btn>
      <v-btn text class="cyan darken-1" v-else
      @click="logout"
      >
        <span class="white--text">Sign Out</span>
        <v-icon right class="white--text">mdi-logout</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer app v-model="drawer" class="primary">
      <v-list shaped class="primary">
        <v-list-item v-for="link in links" :key="link.text" router :to="link.route">
          <!--依然需要引号-->
          <v-list-item-action>
            <v-icon class="white--text">{{link.icon}}</v-icon>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title class="white--text">{{link.text}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>

<script>
import { store } from "../store/index";
import Bmob from "hydrogen-js-sdk";
export default {
  computed: {
    hasLogin() {
      return store.getters.hasLogin;
    },
    curUser() {
      return store.getters.curUser;
    }
  },

  data() {
    return {
      drawer: true,
      links: [
        { icon: "mdi-home", text: "主页", route: "/" },
        { icon: "fa-heart", text: "学习生活", route: "/li" },
        { icon: "fa-users", text: "组团和攒局", route: "/together" },
        {icon: 'fa-star',text:'浙大美食',route:'/xing'},
        {icon: 'fa-bicycle ',text:'浙大周边游',route:'/xingmap'},
        { icon: "fa-address-book", text: "通讯录", route: "/address" }
      ],

      dialog: false,
      valid: true,
      username: "",
      password: "",
      contact: "",

      selectedOp: ['登录'],
      opList: ["注册", "登录"]
    };
  },

  methods: {
    login() {
      console.log(Bmob);
    },
    validate() {
      const query = Bmob.Query("guest");
      if (this.selectedOp == "注册") {
        query.set("username", this.username);
        query.set("password", this.password);
        query.set("contact", this.contact);
        query
          .save()
          .then(res => {
            console.log(res);
            this.dialog = false;
            store.commit("setLogin", true);
            store.commit("setCurUser", this.username);
            this.username = this.password = this.contact = ""
            this.dialog=false
          })
          .catch(err => {
            console.log(err);
            alert("用户名已存在");
          });
      } else {
        console.log("dl");
        query.equalTo("username", "==", this.username);
        query.equalTo("password", "==", this.password);
        query
          .find()
          .then(res => {
            if (res.length == 0) {
              alert("用户名或密码错误");
            } else {
              this.dialog = false;
              store.commit("setLogin", true);
              store.commit("setCurUser", this.username);
              this.username = this.password = this.contact = ""
              this.dialog=false
            }
          })
          .catch(err => {
            console.log(err, "err");
          });
      }
    },

    logout(){
      store.commit('setLogin',false)
      store.commit('setCurUser','')
    }
  }
};

</script>